/* eslint-disable stdlib/jsdoc-doctest-decimal-point */

'use strict';

// MODULES //

import format from '@stdlib/error-tools-fmtprodmsg' ;
import INT8 from '@stdlib/constants-int8-min' ;
import INT16 from '@stdlib/constants-int16-min' ;
import INT32 from '@stdlib/constants-int32-min' ;
import FLOAT16 from '@stdlib/constants-float16-ninf' ;
import FLOAT32 from '@stdlib/constants-float32-ninf' ;
import FLOAT64 from '@stdlib/constants-float64-ninf' ;


// MAIN //

/**
* Returns the minimum value of a specified numeric type.
*
* @param {string} dtype - numeric type
* @throws {TypeError} must provide a recognized numeric type
* @returns {number} minimum value
*
* @example
* var m = typemin( 'int8' );
* // returns -128
*
* @example
* var m = typemin( 'uint8' );
* // returns 0
*
* @example
* var m = typemin( 'int16' );
* // returns -32768
*/
function typemin( dtype ) {
	switch ( dtype ) {
	case 'float64':
		return FLOAT64;
	case 'int32':
		return INT32;
	case 'float32':
		return FLOAT32;
	case 'int8':
		return INT8;
	case 'int16':
		return INT16;
	case 'float16':
		return FLOAT16;
	case 'uint32':
		// Fall-through..
	case 'uint16': // eslint-disable-line no-fallthrough
		// Fall-through..
	case 'uint8': // eslint-disable-line no-fallthrough
		// Fall-through...
	case 'uint8c': // eslint-disable-line no-fallthrough
		return 0;
	default:
		throw new TypeError( format( '0lqCd', dtype ) );
	}
}


// EXPORTS //

export default typemin;
